<template>
  <v-container fluid>
    <v-row align="center" justify="center">
      <v-card max-width="75%" width="500" flat>
        <v-row class="d-flex">
          <h1 class="mb-4">Forma de pago</h1>
        </v-row>
        <v-row>
          <v-radio-group
            v-model="formOkBoy.formaPago"
            col
            @change="clearForm()"
          >
            <v-radio color="secondary" value="Tarjeta" :disabled="pagoTarjetaMaximoFlag">
              <template v-slot:label>
                <div>
                  <img src="../assets/card.svg" width="50px" />
                  Pago con tarjeta
                </div>
              </template>
            </v-radio>
            <v-radio color="secondary" value="Terminal">
              <template v-slot:label>
                <div>
                  <img src="../assets/terminal.svg" width="50px" />
                  Pago con terminal
                </div>
              </template>
            </v-radio>
            <v-radio color="secondary" value="Efectivo">
              <template v-slot:label>
                <div>
                  <img src="../assets/efectivo.svg" width="50px" />
                  Pago en efectivo
                </div>
              </template>
            </v-radio>
          </v-radio-group>
        </v-row>

        <v-form ref="form">
          <v-row class="d-flex">
            <h3 class="mb-4">Informacin del pago</h3>
          </v-row>
          <v-row>
            <v-text-field
              v-model="formOkBoy.titularTarjeta"
              outlined
              label="Titular de la tarjeta:"
              placeholder="Nombre del titular"
              :disabled="formOkBoy.formaPago !== 'Tarjeta' || pagoTarjetaMaximoFlag"
              :rules="nameRules"
              :counter="30"
              maxlength="30"
            ></v-text-field>
          </v-row>
          <v-row>
            <v-text-field
              v-model="formOkBoy.numeroTarjeta"
              outlined
              label="Nmero de la tarjeta:"
              placeholder="16 dgitos"
              :disabled="formOkBoy.formaPago !== 'Tarjeta' || pagoTarjetaMaximoFlag"
              :rules="cardNumberRules"
              :counter="16"
              maxlength="16"
            ></v-text-field>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field
                v-model="formOkBoy.validaTarjeta"
                outlined
                label="Vlida hasta:"
                placeholder="MM/AA"
                :disabled="formOkBoy.formaPago !== 'Tarjeta' || pagoTarjetaMaximoFlag"
                :rules="mmaaRules"
                v-mask="'##/##'"
                maxlength="5"
              ></v-text-field>
            </v-col>
            <v-col>
              <v-text-field
                v-model="formOkBoy.cvvTarjeta"
                outlined
                label="CVV:"
                placeholder="***"
                :disabled="formOkBoy.formaPago !== 'Tarjeta' || pagoTarjetaMaximoFlag"
                :rules="cvvRules"
                :counter="3"
                maxlength="3"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-form>
        <v-row>
          <v-col>
            <v-btn color="secondary" text block @click="back()">Atrs</v-btn>
          </v-col>
          <v-col>
            <v-btn class="buttonPrimary" block @click="next()">Siguiente</v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { mapFields } from "vuex-map-fields";
import { JSEncrypt } from "jsencrypt";
import pubKey from "./../statics/publicKey";
import { mask } from "vue-the-mask";

export default {
  computed: {
    ...mapFields(["landingCounter", "formOkBoy", "pagoTarjetaMaximoFlag"]),
  },
  directives: {
    mask,
  },
  data() {
    return {
      nameRules: [
        (v) => !!v || "El nombre es requerido.", //
      ],
      cardNumberRules: [
        (v) => !!v || "El nmero de tarjeta es requerido.", //
        (v) => (v && v.length <= 16) || "El nmero debe ser de 16 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
      mmaaRules: [
        (v) => !!v || "La vigencia es requerida.", //
        (v) => (v && v.length <= 5) || "El nmero debe ser de 4 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
      cvvRules: [
        (v) => !!v || "El cvv es requerido.", //
        (v) => (v && v.length <= 3) || "El nmero debe ser de 3 dgitos",
        (v) => RegExp("[0-9]+$").test(v) || "Ingresa slo nmeros",
      ],
    };
  },
  methods: {
    next() {
      switch (this.formOkBoy.formaPago) {
        case "Tarjeta":
          if (this.$refs.form.validate()) {
            this.formOkBoy.formaPagoID = 3;
            this.encryptData();
            this.landingCounter++;
          }
          break;
        case "Terminal":
          this.formOkBoy.formaPagoID = 2;
          this.landingCounter++;
          break;
        case "Efectivo":
          this.formOkBoy.formaPagoID = 1;
          this.landingCounter++;

          break;
      }
    },
    back() {
      this.clearForm();
      this.landingCounter--;
    },
    clearForm() {
      this.$refs.form.reset();
    },
    encryptData() {
      let card = {
        number: "",
        cvv: "",
        name: "",
        expiration: "",
      };
      let encrypt = new JSEncrypt();
      encrypt.setPublicKey(pubKey.publicKey);
      card.number = encrypt.encrypt(this.formOkBoy.numeroTarjeta);
      card.cvv = encrypt.encrypt(this.formOkBoy.cvvTarjeta);
      card.name = encrypt.encrypt(this.formOkBoy.titularTarjeta);
      card.expiration = encrypt.encrypt(this.formOkBoy.validaTarjeta);
      this.formOkBoy.cardCrypto = card;
    },
  },
  watch: {
    pagoTarjetaMaximoFlag: function (val) {
      if(val) {
        this.clearForm()
      }
    }
  }
};
</script>

<style scoped>
.theme--light.v-sheet {
  background-color: #f1f3f6;
  border-color: #f1f3f6;
  color: rgba(0, 0, 0, 0.87);
}
</style>